.hero-title{grid-column:1/-1;height:calc(4.5em + 1rem);font-size:var(--display-size)}@media screen and (max-width: 1919px){.hero-title{height:calc(5.625em + 1rem)}}@media screen and (max-width: 539px){.hero-title{height:calc(6.75em + 1rem)}}@media screen and (max-width: 424px){.hero-title{height:calc(7.875em + 1rem)}}@media screen and (max-width: 319px){.hero-title{height:calc(9em + 1rem)}}.hero-title #quotes{padding:0 .125em;color:var(--surface-color);background:var(--foreground-tint-color)}@media screen and (max-width: 539px){.hero-title #quotes{padding:0}}.hero-title .-accent{color:var(--foreground-color) !important;background:var(--foreground-dimmed-color)}.hero-title .cursor{width:0;outline:.05em solid var(--foreground-color)}.hero-title .cursor.-blink{animation:blink .8s infinite}@keyframes blink{50%{opacity:0}}