.row{display:grid;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);padding:var(--grid-gap) 0;border-bottom:1px solid var(--border-tint-color);overflow:hidden}.row.-fit{display:block;border:unset}.row.-flow{display:block;-moz-column-count:4;column-count:4;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--margin)}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 639px){.row.-flow{-moz-column-count:auto;column-count:auto}}.row:last-child{border:none}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 639px){.row{grid-template-columns:auto}}