html{display:flex}body{display:flex;width:100%;color:var(--foreground-color);background:var(--surface-color)}#root{width:100%}main,section{display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap)}@media screen and (max-width: 1023px){main,section{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 643px){main,section{grid-template-columns:repeat(2, 1fr)}}main{max-width:var(--body);padding:0 var(--margin);margin:0 auto}main hr{grid-column:1/-1}section{padding:var(--margin) 0;color:var(--foreground-color);background:var(--surface-color)}section:first-child{padding-top:calc(var(--header)*2.5)}section:last-child{padding-bottom:calc(var(--header)*2.5)}section.content{background:var(--surface-color);box-shadow:calc((100vw - var(--body))*.5 + var(--margin)) 0 0 var(--surface-color),calc((100vw - var(--body))*-0.5 - var(--margin)) 0 0 var(--surface-color)}@media screen and (max-width: 1919px){section.content{box-shadow:calc(var(--margin)*2) 0 0 var(--surface-color),calc(var(--margin)*-2) 0 0 var(--surface-color)}}